import React, { useEffect, useState } from "react";
import { connect } from 'react-redux';
import { View, Image, Text, } from "@tarojs/components";
import Taro, { useDidShow } from '@tarojs/taro';

import './recomWait.less';
import { RecomWaitProps } from "./recomWaitInterface";
import Btn from "./component/Btn";
import NavBar from "@/components/NavBar";

const RecomWait: React.FC<RecomWaitProps> = (props) => {

  const { dispatch, mine, } = props;
  const { mineInfo, globalConfig, } = mine;

  const [isOpen, setIsOpen] = useState(false);


  useEffect(() => {
    setIsOpen(!!globalConfig.auntInviteToggle);
  }, [globalConfig.auntInviteToggle]);


  useDidShow(() => {
    dispatch({ type: 'mine/queryAuntMine' });
    dispatch({ type: 'mine/globalConfigQuery' });
  });


  const _ss = () => {
    let _arr = isOpen ? ['1.邀请好友', '2.好友注册', '3.获取奖励'] : ['1.邀请好友', '2.好友注册', '3.门店审核'];

    return _arr.map(item =>
      <View key={item} className='rec2-21'>{item}</View>)
  };

  return <View className="recom-wait">
    <NavBar
      title="转介绍"
      // noBg
      titleStyle={{ color: '#fff !important' }}
      propBarStyle={{ background: 'transparent' }}
    />

    <Image src={`https://oss.bm001.com/static/ka/a/${isOpen ? 'kqjl' : 'wkqjl'}-banner.png`} className="rec1" />

    <View
      className="rec3"
      onClick={() => {
        Taro.navigateTo({ url: '/pages/mine/cumulatReward/cumulatReward' })
      }}
    >
      <Text className="rec3-text">查看介绍记录</Text>
      <Text className="iconfont iconyoujiantou rec3-icon" />
    </View>

    <View className="rec6">

      <View className="rec2">
        <View className="rec2-1">
          <View className="rec2-11">
            <Text className="rec2-111">￥</Text>
            <Text className="rec2-112">{(mineInfo.inviteReward || 0) / 100}</Text>
          </View>
          <View className="rec2-12">累计奖励(元)</View>
        </View>
        <View className="rec2-2" />
        <View className="rec2-1">
          <View className="rec2-11 rec2-13">{mineInfo.inviteNum || 0}</View>
          <View className="rec2-12">累计介绍(人)</View>
        </View>
      </View>

      <View className="rec2-15">
        <Image src={`https://oss.bm001.com/static/ka/a/link.png`} className="rec2-16" />
      </View>

      <View className="rec2-17">

        <Btn text={'转介绍流程'} />

        <View className="rec2-18">
          <Image src={`https://oss.bm001.com/static/ka/a/${isOpen ? '' : 'no-'}open.png`} className='rec2-19' />
          <View className="rec2-20">{_ss()}</View>
        </View>

        <View className="rec2-22">
          <Image src={`https://oss.bm001.com/static/ka/a/dashed.png`} className='rec2-25' />
        </View>

        <Btn text={'奖励说明'} />

        <View className="rec2-26">
          {
            isOpen ? '您的好友扫码海报或点击链接即可打开注册页面，填写注册信息并点击提交，家政公司即可对信息进行审核，审核成功后，您可联系家政公司线下索取奖励。'
              : '您的好友扫码海报或点击链接即可打开注册页面，填写注册信息并点击提交，家政公司即可对信息进行审核，审核成功后，您的好友即成为家政服务员。'
          }
        </View>

      </View>

      {
        isOpen && <React.Fragment>
          <View className="rec2-15">
            <Image src={`https://oss.bm001.com/static/ka/a/link.png`} className="rec2-16" />
          </View>

          <View className="rec2-27">
            <View className="rec2-28">
              <Text className="rec2-29">成功介绍一人可获得</Text>
              <Text className="rec2-30"> ￥</Text>
              <Text
                className="rec2-31">{globalConfig.auntInviteReward ? globalConfig.auntInviteReward / 100 : 0} </Text>
              <Text className="rec2-29">元奖励</Text>
            </View>
            <View className="rec2-32">奖励由 {mineInfo.shopName} 提供</View>
          </View>
        </React.Fragment>
      }


    </View>


    <View
      className="rec5"
      onClick={() => {
        Taro.navigateTo({ url: `/pages/mine/posterDetail/posterDetail` })
      }}
    >点击立即邀请</View>

  </View>

}


export default connect(({ mine, dispatch, loading }) => ({
  mine,
  dispatch,
  loading: loading.effects['findAunt/auntPossibleAdd'],
}))(RecomWait);
